:root {
  --primaryBlue: #6cbfda;
  --secondaryBlue: #b2cede
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #f0f0f0;
  /* background-image: linear-gradient(60deg, #64b3f4 0%, #c2e59c 100%); */
  color: #4e505a;
}

* {
  scrollbar-width: thin;
  scrollbar-color: var(--primaryBlue) var(--primaryBlue);
}
*::-webkit-scrollbar {
  width: 5px;
}
*::-webkit-scrollbar-track {
  background: var(--secondaryBlue);
  border-radius: 20px;
}
*::-webkit-scrollbar-thumb {
  background-color: var(--primaryBlue);
  border-radius: 20px;
  border: 3px solid var(--primaryBlue);
}

.btn-primary {
  background-color: var(--primaryBlue) !important;
  border-color: var(--primaryBlue) !important;
}

.btn-outline-primary {
  color: var(--primaryBlue) !important;
  border-color: var(--primaryBlue) !important;
}

.btn-outline-primary:hover {
  background-color: var(--primaryBlue) !important;
  color: white !important;
}

.btn-check:active+.btn-outline-primary, .btn-check:checked+.btn-outline-primary, .btn-outline-primary.active, .btn-outline-primary.dropdown-toggle.show, .btn-outline-primary:active {
  color: #fff !important;
  background-color: var(--primaryBlue);
  border-color: var(--primaryBlue);
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.windowContainer {
  width: 80%;
  position: relative;
  left: 0;
  right: 0;
}


#additionalSymptomsSearchResults{
  /* width: max(500px); */
  overflow-y:auto;
  overflow-x: auto;
  max-height: 15vh !important;
  border: none;
}

#labSearchResults {
  overflow-y:auto;
  overflow-x: auto;
  max-height: 25vh !important;
  border: none;
}

#riskSearchResults {
  overflow-y:auto;
  overflow-x: auto;
  max-height: 22vh !important;
  border: none;
  scrollbar-color: blue white !important;
}

.riskfactorWrapper {
  position: relative;
}

.riskFactorButtons {
  position: relative;
  max-height: 78px !important;
  max-width: 100%;
  overflow-x: auto;
  flex-wrap: wrap;
  justify-content: space-around;
}

.dropdown-item {
  width: auto;
}

.hidden {
  display: none!important;
}
#selectedAge{
  font-size: 20px;
  margin-top: 1px;
  height: 35px;
}

#inputGender {
  padding: 0 1px 0 1px;
  border-top: 0;
  border-radius: 0;
}

#selectedAge.form-control::-webkit-input-placeholder {
  color: #4e505a;
  font-size: 20px;
}
#selectedAge.form-control:-moz-placeholder {
  color: #4e505a;
  font-size: 20px;
}
#selectedAge.form-control::-moz-placeholder {
  color: #4e505a;
  font-size: 20px;
}
#selectedAge.form-control::placeholder {
  color: #4e505a;
  font-size: 20px;
}
#selectedAge.form-control:-ms-input-placeholder {
  color: #4e505a;
  font-size: 20px;
}

#labSearchResult:hover{
  background-color: #7e7e7e4d;
  cursor: pointer;
}

#additionalQuestionText {
  border: 0;
}

.toggleTab {
  -webkit-touch-callout: none; 
    -webkit-user-select: none; 
     -khtml-user-select: none; 
       -moz-user-select: none; 
        -ms-user-select: none; 
            user-select: none; 
  cursor: pointer;
}

#individualSymptomSearchResult {
  padding-top: 0!important;
  padding-bottom: 5px!important;
}

#individualSymptomSearchResult:hover {
  background-color: #7e7e7e28;
}

#patientNoteContainer, #rightDisplay {
  /* width: 50%!important; */
  position: relative;
  margin: auto;
  border-radius: 10px;
  max-height: 90vh;
  max-width: 700px !important;
}

/* #patientNoteContainer  {
  margin-right: 5px;
} */

#rightDisplay {
  margin-left: 5px;
}

.roundedCorners {
  border-radius: 10px;
  /* box-shadow:  0px 1px 3px 0px rgba(0, 0, 0, 0.2),
  0px 1px 1px 0px rgba(0, 0, 0, 0.14),
  0px 2px 1px -1px rgba(0, 0, 0, 0.12);  */
}

.roundedCorners span {
  text-align: left;
}

#patientMainNote {
  border-radius: 10px;
  height: 100%;
  box-shadow:0px 1px 8px 0px rgba(0, 0, 0, 0.2),
    0px 3px 4px 0px rgba(0, 0, 0, 0.14),
    0px 3px 3px -2px rgba(0, 0, 0, 0.12); 
}

#rightDisplay {
  max-height: 90vh;
}

#patientNoteContainer {
  border: 0;
  border-color: rgba(255, 255, 255, 0) !important;
 
}

.notarget {
  pointer-events: none;
}

.listItem {
  margin-left: 25px;
}

.category {
  font-size: 20px;
  font-weight: bold;
  justify-content: start;
}

#ccSearch {
  opacity: .4;
  width: 17px;
}
#ccSearch:hover {
  opacity: 1;
  cursor: pointer;
}

#ccSearchContainer, #symptomsSearchContainer, #labsMainContainer,
#risksMainContainer{
  position: absolute;
  width: 100%;
  height: 100%;
  z-index: 30;
  margin: auto;
  background-color: #00000000 ;
  border:0
}
#ccSearchContainerTop, #symptomsSearchContainerTop, #labsSearchContainerTop,
#risksSearchContainerTop {
  position: absolute;
  width: 100%;
  height: 50vh;
  z-index: 31;
  margin: auto;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  /* text-align: center; */
  background-color: white;
  border-radius: 10px;
  overflow-y: hidden;
  box-shadow: 0px 3px 5px -1px rgba(0, 0, 0, 0.2),
    0px 6px 10px 0px rgba(0, 0, 0, 0.14),
    0px 1px 18px 0px rgba(0, 0, 0, 0.12);
}
#ccSearchContainerBottom, #symptomsSearchContainerBottom, #labsSearchContainerBottom,
#risksSearchContainerBottom {
  position: absolute;
  top: 0;
  width: 100%;
  z-index: 30;
  height: 100%;
  background-color: #6d6d6d41!important;
  border-radius: 10px;
  /* box-shadow: 0px 10px 13px -6px rgba(0, 0, 0, 0.2),
    0px 20px 31px 3px rgba(0, 0, 0, 0.14),
    0px 8px 38px 7px rgba(0, 0, 0, 0.12); */
}


#ccSearchResultOutput {
  overflow-y: scroll;
}

#additionalSymptomsSearchResultsContainer {
  height: 10%;
  margin-bottom: 50px;
  border: none;
}

#labTopButtonsContainer {
  display: flex;
  justify-content: space-around;
  flex-wrap: wrap;
  max-height: 6vh !important;
  overflow-y: auto;
  overflow-x: hidden;
}

.progress {
  height: 7px;
}

.faded {
  opacity: .5;
}

/* .faded:hover {
  opacity: 1;
} */

.ageGender {
  font-size: 12px;
  text-align: start;
  margin-left: 15px;
  position: relative;
  top: 10px;
  font-weight: bold;
}

.ageGenderGrid {
  position: relative;
  left: -12px;
  display: grid;
  grid-template-rows: 1fr 3fr;
  grid-template-columns: 75px 90px auto;
  width: 100%;
  height: 60px;
}

.age {
  border-right: solid 1px black;
}

.ageInput {
  margin-left: 7px;
}

.ccInput {
  padding-top: 3px;
  padding-left: 3px;
  color: #4e505a;
}

.sex {
  padding-left: 6px;
}

.color {
  color: #4e505a !important;
}

.addSymptomsCard {
  max-height: 40vh !important;
  overflow-y: auto;
  display: flex;
  flex-direction: column;
}

.addSymptomsCard h5 {
  padding-bottom: 0;
  margin-bottom: 0;
}

.presentSymptoms {
  max-height: 30vh !important;
  overflow-y: auto;
}

.presentSymptoms button {
  text-align: left !important;
}

.addSymptomsDisplay {
  display: flex;
  flex-direction: column;
}

.symptomsFont {
  font-size: 14px;
  padding: 0;
}

.questionIcon {
  width: 15px;
  margin-left: 5px;
  transition-duration: .2s;
  position: relative;
  top: -2px;
}
.questionIcon:hover {
  cursor: pointer;
  transform: scale(1.1);
  transition-duration: .2s;
}

.riskFactorIcons {
  display: grid;
  height: 50px;
  grid-template-columns: 1fr;
  grid-template-rows: 2fr 1fr;
  padding: 3px;
  margin: 0;
  color: black;
}

.riskFactorIcons span {
  font-size: 12px;
}

.riskFactorIcons:hover{
  background-color: var(--secondaryBlue);
  border-radius: 4px;
  cursor: pointer;
}

.untargetable {
  pointer-events: none;
}

.svgIcon {
  width: 30px;
}

.centerIcon {
  display: flex;
  flex-direction: row;
  justify-content: center;
}

.text {
  text-align: center;
}

.highlight, .active {
  background-color: var(--secondaryBlue);
  border-radius: 4px;
}

.flagIcon {
  display: flex;
  flex-direction: column;
  justify-content: center;
  color: rgb(211, 0, 0);
  transition-duration: .1s;
  opacity: .2;
}

.flagIcon:hover {
  cursor: pointer;
  /* transform: scale(1.1); */
  transition-duration: .1s;
  opacity: 1;
}

.ddxExplainTitle {
  text-align: center;
  font-size: 22px;
}

.ddquestionIcon, .rationaleQuestion {
  position: absolute !important;
  right: 10px;
}

.ddxExplainGrid {
  display: grid;
  grid-template-columns: 1fr 1fr;
  overflow: auto;
  padding: 0 30px 10px 30px;
}
.ddxExplainGrid div {
  padding-right: 5px;
}
.ddxExplainGrid div ul {
  padding-left: 5px;
}
.ddxExplainGrid div div {
  text-decoration: underline;
}

.ddxExplainContainer {
  position: relative;
  margin: auto;
  margin-top: 10px;
  max-height: 50vh;
  max-width: 95%;
  z-index: 50;
}

.evidenceText {
  font-size: 14px;

}

#rationaleOutputElement {
  margin: 5px 0 5px 0;
}

#searchCCButton {
  margin-left: 5px;
}

#addSymptomsearch {
  margin-top: 3px;
}

.closeButtonMargin {
  margin-top: 3px;
}

.appLogo {
  height: 10%;
  margin: auto;
  margin-top: 2%;
  margin-bottom: 2%;
  position: relative;
  top: 0;
  left: 0;
  right: 0;
  display: block;
}

.windowContainer {
  top: 0;
  position: absolute;
}

@media (max-width: 992px) {
  .windowContainer {
    width: 100%;
    position: relative;
    left: 0;
    right: 0;
    top: 0;
  }
}

@media (max-width: 767px) {

  #ccSearchContainerTop, #symptomsSearchContainerTop, #labsSearchContainerTop,
  #risksSearchContainerTop {
    position: absolute;
    top:0;
    right: 0;
    bottom: unset;
    left: 0;
    width: 100%;
    height: 80vh;
    margin: auto;
  }

  .ageGender {
    margin-left: 0;
    text-align: left;
    border: none;
  }
  .ageInput {
    border: none;
    z-index: 5;
  }
  .ageGenderGrid {
    margin-left: 15px;
    grid-template-columns: 1fr 1fr;
    grid-template-rows: 1fr 1fr 1fr;
  }
  
  .input1, .age1 {
    grid-column: 1/3;
    grid-row: 1/2;
  }
  .input2, .age2 {
    grid-column: 1/3;
    grid-row: 2/3;
  }
  .input3, .age3 {
    grid-column: 1/3;
    grid-row: 3/4;
  }
  .input1 {
    margin-left: 23px;
    padding-top: 1px;
  }
  .input2 {
    margin-left: 27px;
    padding-top: 3px;
  }
  .input3 {
    margin-left: 110px;
  }

  .ageGenderGrid input, .ageGenderGrid button,
  .ageGenderGrid input::placeholder
  {
    /* width: 25px !important; */
    font-size: 14px !important;
  }

  .ageGenderGrid .ageInput {
    height: 30px !important;
    font-size: 15px !important;
  }

  #patientNoteContainer {
    height: auto !important;
    max-height: none;
  }

  #labTopButtonsContainer, .riskFactorButtons {
    max-height: 20% !important;
  
  }

  .title {
    margin: 0;
  }

  #riskSearchResults, #labSearchResults {
    max-height: 38vh !important;
  }

  .minimizedContainer {
    padding: 20px !important;
  }
}